import React, { useContext } from 'react'
import './sipariver.css'
import etdrm from '../../img/etdrm.png'
import etekmek from '../../img/ekmeketdoner.png'
import tavukekmek from '../../img/TavukEkmek.png'
import tavukdrm from '../../img/tavukdrm.png'
import { Context } from '../Context/SiteContext'
import MovingComponent from 'react-moving-text'



function Sipari() {
  const {
    firstname, setFirstname,
    addres, setAddres,
    tel, setTel,
    soan, setSoan,
    seim, setSeim,
    ketcapMayonez, setKetcapMayonez,
    ekistek, setEkistek,
    sipari, setSipari } = useContext(Context)

  const handlelocale = () => {
    setSipari(
      [...sipari, { firstname, addres, tel, soan, seim, ketcapMayonez, ekistek }]
    )
    let json = JSON.stringify(sipari)
    localStorage.setItem("sipari", json)
  }
  return (
    <div className='content'>
      <MovingComponent
        type="squeezeVertical"
        duration="1000ms"
        delay="0s"
        direction="normal"
        timing="ease"
        iteration="infinite"
        fillMode="none">
        <h2>Haydi dnerimizi zelletirelim...</h2>
      </MovingComponent>
      <div className='spiaris'>
        <form className='form'>
          <div className='isim'>
            <label htmlFor="firstName">Sipari Verenin Adnz Soyad : </label>
            <input
              id="firstName"
              name="firstName"
              placeholder="sim giriniz"
              autoComplete='off'
              onChange={(e) => { setFirstname(e.target.value) }} />
          </div>
          <div className='adress'>
            <label htmlFor="addres">Sipariinizi hangi adrese getirelim : </label>
            <textarea
              id="addres"
              name="addres"
              placeholder="Sipari adresi giriniz"
              autoComplete='off'
              onChange={(e) => { setAddres(e.target.value) }} />
          </div>
          <div className='telefon'>
            <label htmlFor="tel">Size ulaabilmek iin telefon numaranz : </label>
            <input
              id="tel"
              name="tel"
              type="number"
              autoComplete='off'
              placeholder='0555 555 55 55'
              onChange={(e) => { setTel(e.target.value) }}
            />
          </div>
          <div className='dnersections'>
            <input type="image" src={etdrm} alt="etdrm" value="ET DRM" onClick={(e) => {
              e.preventDefault();
              setSeim(e.target.alt)
            }} />
            <input type="image" src={etekmek} alt="etekmek" value="EKMEK ARASI ET" onClick={(e) => {
              e.preventDefault();
              setSeim(e.target.alt)
            }} />
            <input type="image" src={tavukekmek} alt="tavukekmek" value="EKMEK ARASI TAVUK" onClick={(e) => {
              e.preventDefault();
              setSeim(e.target.alt)
            }} />
            <input type="image" src={tavukdrm} alt="tavukdrm" value="TAVUK DRM" onClick={(e) => {
              e.preventDefault();
              setSeim(e.target.alt)
            }} />
          </div>
          <div className="soan">
            <label>
              <input
                type="radio"
                name="checked"
                value="Soanl"
                onChange={(e) => { setSoan(e.target.value) }} />
              Soanl
            </label>
            <label>
              <input
                type="radio"
                name="checked"
                value="Soansz"
                onChange={(e) => { setSoan(e.target.value) }} />
              Soansz
            </label>
            <label>
              <input
                type="radio"
                name="checked"
                value="Az soanl"
                onChange={(e) => { setSoan(e.target.value) }} />
              Az soanl
            </label>
          </div>
          <div className="ketcapgroup">
            <label>
              <input
                type="radio"
                name="kchecked"
                value="Ketap istiyorum"
                onChange={(e) => { setKetcapMayonez(e.target.value) }} />
              Ketap istiyorum
            </label>
            <label>
              <input
                type="radio"
                name="kchecked"
                value="Mayonez istiyorum"
                onChange={(e) => { setKetcapMayonez(e.target.value) }} />
              Mayonez istiyorum
            </label>
            <label>
              <input
                type="radio"
                name="kchecked"
                value="Hem ketap hem mayonez istiyorum"
                onChange={(e) => { setKetcapMayonez(e.target.value) }} />
              Hem ketap hem mayonez istiyorum
            </label>
            <label>
              <input
                type="radio"
                name="kchecked"
                value="stemiyorum"
                onChange={(e) => { setKetcapMayonez(e.target.value) }} />
              stemiyorum
            </label>
          </div>
          <div className='ekistek'>
            <label htmlFor="ekistek">Ekstra eklemek istedikleriniz: </label>
            <textarea
              id="ekistek"
              name="ekistek"
              autoComplete='off'
              placeholder="Eklemek istediklerinizi yaznz"
              onChange={(e) => { setEkistek(e.target.value) }} />
          </div>
        </form>
        <div className='sipariiniz'>
          <h1>Tanmlanan Sipari:</h1>
          <ul>
            <li>{seim}</li>
            <li>{firstname}</li>
            <li>{addres}</li>
            <li>{tel}</li>
            <li>{soan}</li>
            <li>{ketcapMayonez}</li>
            <li>{ekistek}</li>
          </ul>
          <button onClick={handlelocale}>Sparii Tamamla</button>
        </div>
      </div>
    </div>
  )
}

export default Sipari