import React, { useContext, useEffect } from 'react'
import './Siparilerim.css'
import {Context} from '../Context/SiteContext'

function Siparilerim() {
 const{sipari,setSipari}=useContext(Context)

  useEffect(()=>{
    const data1 = JSON.parse(localStorage.getItem('sipari'));
    setSipari(data1);
  },[setSipari]);

  const removeItem = (index) => {
    sipari.splice(index,1);
    localStorage.setItem('sipari',JSON.stringify(sipari));
    setSipari([...sipari])
  }

  if(localStorage===null || (sipari.length!==0)){
    return (
      <div>
        {sipari.map((i,index)=>{
          return (
          <div className="card" key={index}>
            <>
              <p>{`Merhaba ${i.firstname} sipariiniz aldk.En ksa srede adresinize ulatracaz.`}</p>
              <p>{`Sipari detay : ${i.seim} - ${i.soan} - ${i.ketcapMayonez} - ${i.ekistek}`}</p>
              <p>{`Sipari Adresi : ${i.addres}`}</p>
            </>
            <div className='remove'>
                <button onClick={removeItem}>Siparii Sil</button>
              </div>
          </div>)
        })}
      </div>)
      }
    else{
      return(
        <div className='sipariyok'>
          Girilen bir sipari yoktur...
        </div>
    )}

}

export default Siparilerim